import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Countries from './Countries';
import Search from './Search';
import "./CountryDetails.css"
import listOfCountries from './listOfCountries';
const Home = () => {
  let countryName;
  let countryRegion;
  let [countries, updateCountries] = useState(listOfCountries);

  const getCountryName = (event) => {
    let filteredCountries = listOfCountries.filter(country => {

     if ((country.name.toLowerCase().indexOf(event.target.value.toLowerCase())) !== -1) {
        return country;
      }
    })
    updateCountries(filteredCountries);
    console.log(countries);
  }
  const getCountryRegion = (event) => {
    console.log(event.target.value);
    let filteredCountries = listOfCountries.filter(country => {
      if (country.region.indexOf(event.target.value) !== -1) {
        return country;
      }
    })
    updateCountries(filteredCountries);
    console.log(countries);
  }
  return (
    <div>
      <Search
        countryRegion={countryRegion}
        onselect={getCountryRegion}
        onchange={getCountryName}
        countryName={countryName}
      />

      <div className='country'>
        {countries.map((country, i) => {

          return (

            <Link className='link' to={`/CountryDetails/${country.name}`}>
              <Countries
                key={i}
                flag={country.flag}
                name={country.name}
                population={country.population}
                region={country.region}
                capital={country.capital}
              />
            </Link>
          )
        })}
      </div>
    </div>

  )
}
export default Home;