import React from 'react';
import './Search.css'

const Search = ({onchange, countryName, onselect, countryRegion}) => {
  return (
    <div className='search'>
      <input placeholder='Search for a country...' type='text' onChange={onchange} value={countryName}></input>
      <select value={countryRegion} onChange={onselect}>
      <option>Filter by Region</option>
        <option>Africa</option>
        <option>Americas</option>
        <option>Asia</option>
        <option>Europe</option>
        <option>Oceania</option>
      </select>
    </div>
  )
}
export default Search;