import React, { useEffect, useState } from 'react';
import './CountryDetails.css';
const CountryDetails = ({ match }) => {
  useEffect(
    () => {
      console.log(match);
      fetchCountryDetail();
    },[])
  let [countryInfo, getCountryInfo] = useState([]);
  const fetchCountryDetail = async () => {
    const data = await fetch(`https://restcountries.eu/rest/v2/name/${match.params.country}`);
    let countryDetail = await data.json();
    getCountryInfo(countryDetail);
    console.log(countryDetail);
  }
  const Button = () => {
    return (
      <button className='back-button' onClick={() => {
        console.log('go back');
        window.history.back();
      }}>Back</button>

    )
  }
  return (
    <div>
      <div className='country-details'>
        <Button />
        {countryInfo.map((countryIdentity,i) => {
          return (
            <div className='country-info' id={i}>
              <div className='country-image'>
                <img src={countryIdentity.flag} alt='flag' />
              </div>
              <div className='country-detail'>
                <h1 className='country-name'>{countryIdentity.name}</h1>
                <div className='other-details'>
                  <div className='first-item'>
                    <h4 >Native Name: {countryIdentity.nativeName} </h4>
                    <h4>Population: {countryIdentity.population} </h4>
                    <h4>Region: {countryIdentity.region}</h4>
                    <h4>Sub Region:{countryIdentity.subregion}</h4>
                    <h4>Capital: {countryIdentity.capital} </h4>
                  </div>
                  <div className='second-item'>
                  <h4>Calling Code: +{countryIdentity.callingCodes[0]}</h4>
                    <h4>Top Level Domain: {countryIdentity.topLevelDomain[0]}</h4>
                    <h4>Currencies: {countryIdentity.currencies.map((currency) => {
                      return (
                        currency.name
                      )
                    })}</h4>
                      <h4 className='languages'>Languages: {countryIdentity.languages.map((languages) => {
                        return (
                          <h4>{languages.name}</h4>
                        )
                      })}</h4>

                  </div>
                  <h3 className='border-list'>Border Countries: {countryIdentity.borders.map((borders, i) => {
                    return <button className='border-button' id={i}>{borders}</button>
                  }
                  )}</h3>
                </div>
              </div>

            </div>
          )
        })}


      </div>


    </div>

  )
}
export default CountryDetails